	module.exports = {"data":{"post":{"html":"<p><a href=\"http://www.ndrinfo.de/\">NDRInfo</a> radio this morning broadcast a short profile of Volkswagen brand chairman <a href=\"http://www.volkswagen-ag.de/english/docs/2a-bernhard.html\">Wolfgang Bernhard</a>, the cost-cutting manager who formerly worked for Chrysler in the US. He's apparently very dry and businesslike. When asked the difference between working in the US and in Germany, he replied:</p>\n<blockquote><p>\n  In Germany I am called Dr. Bernhard, in the US I was called Wolfgang. Otherwise the problems are just the same.\n</p></blockquote>\n<p>An attempt to make the interview more personal failed. \"How do you relax in your spare time?\"</p>\n<blockquote><p>\n  I sleep.\n</p></blockquote>","fields":{"title":"I Sleep","date":"29 Sep 2006","url":"https://papascott.de/2006-09-29-i-sleep/"}}},"pathContext":{"slug":"/2006-09-29-i-sleep/","prev":{"fields":{"title":"Germany Invaded By Giant Insect","date":"29 Sep 2006","slug":"/2006-09-29-germany-invaded-by-giant-insect/"}},"next":{"fields":{"title":"Microwave Chocolate-Chip Pancakes and Sausage on a Stick","date":"30 Sep 2006","slug":"/2006-09-30-microwave-chocolate-chip-pancakes-and-sausage-on-a-stick/"}}}}

